import { NextRequest, NextResponse } from 'next/server';
import connectDB from '@/lib/db';
import Cart from '@/lib/models/Cart';
import Product from '@/lib/models/Product';
import { getUserFromRequest } from '@/lib/middleware';
import { successResponse, errorResponse } from '@/lib/api-response';

// Get user cart
export async function GET(request: NextRequest) {
    const user = getUserFromRequest(request);
    if (!user) {
        const { response, status } = errorResponse('Authentication required', 401);
        return NextResponse.json(response, { status });
    }

    try {
        await connectDB();

        const cart = await Cart.findOne({ user: user.userId }).populate('items.product').lean();

        if (!cart) {
            return NextResponse.json(successResponse({ items: [], total: 0 }));
        }

        // Calculate total
        const total = cart.items.reduce((sum, item: any) => sum + item.price * item.quantity, 0);

        return NextResponse.json(successResponse({ items: cart.items, total }));
    } catch (error: any) {
        console.error('Get cart error:', error);
        const { response, status } = errorResponse('Failed to fetch cart');
        return NextResponse.json(response, { status });
    }
}

// Add item to cart
export async function POST(request: NextRequest) {
    const user = getUserFromRequest(request);
    if (!user) {
        const { response, status } = errorResponse('Authentication required', 401);
        return NextResponse.json(response, { status });
    }

    try {
        await connectDB();

        const { productId, quantity = 1 } = await request.json();

        if (!productId) {
            const { response, status } = errorResponse('Product ID is required');
            return NextResponse.json(response, { status });
        }

        const product = await Product.findById(productId);
        if (!product) {
            const { response, status } = errorResponse('Product not found', 404);
            return NextResponse.json(response, { status });
        }

        // Check stock
        if (product.trackQuantity && product.quantity < quantity) {
            const { response, status } = errorResponse('Insufficient stock');
            return NextResponse.json(response, { status });
        }

        let cart = await Cart.findOne({ user: user.userId });

        if (!cart) {
            cart = await Cart.create({
                user: user.userId,
                items: [{ product: productId, quantity, price: product.price }],
            });
        } else {
            const existingItem = cart.items.find(
                (item: any) => item.product.toString() === productId
            );

            if (existingItem) {
                existingItem.quantity += quantity;
                existingItem.price = product.price;
            } else {
                cart.items.push({ product: productId, quantity, price: product.price });
            }

            await cart.save();
        }

        await cart.populate('items.product');

        return NextResponse.json(successResponse(cart, 'Item added to cart'));
    } catch (error: any) {
        console.error('Add to cart error:', error);
        const { response, status } = errorResponse('Failed to add to cart');
        return NextResponse.json(response, { status });
    }
}

// Update cart item quantity
export async function PUT(request: NextRequest) {
    const user = getUserFromRequest(request);
    if (!user) {
        const { response, status } = errorResponse('Authentication required', 401);
        return NextResponse.json(response, { status });
    }

    try {
        await connectDB();

        const { productId, quantity } = await request.json();

        if (!productId || quantity === undefined) {
            const { response, status } = errorResponse('Product ID and quantity are required');
            return NextResponse.json(response, { status });
        }

        const cart = await Cart.findOne({ user: user.userId });

        if (!cart) {
            const { response, status } = errorResponse('Cart not found', 404);
            return NextResponse.json(response, { status });
        }

        const item = cart.items.find((item: any) => item.product.toString() === productId);

        if (!item) {
            const { response, status } = errorResponse('Item not in cart', 404);
            return NextResponse.json(response, { status });
        }

        if (quantity <= 0) {
            cart.items = cart.items.filter((item: any) => item.product.toString() !== productId);
        } else {
            item.quantity = quantity;
        }

        await cart.save();
        await cart.populate('items.product');

        return NextResponse.json(successResponse(cart, 'Cart updated'));
    } catch (error: any) {
        console.error('Update cart error:', error);
        const { response, status } = errorResponse('Failed to update cart');
        return NextResponse.json(response, { status });
    }
}

// Delete item from cart
export async function DELETE(request: NextRequest) {
    const user = getUserFromRequest(request);
    if (!user) {
        const { response, status } = errorResponse('Authentication required', 401);
        return NextResponse.json(response, { status });
    }

    try {
        await connectDB();

        const { searchParams } = new URL(request.url);
        const productId = searchParams.get('productId');

        if (!productId) {
            const { response, status } = errorResponse('Product ID is required');
            return NextResponse.json(response, { status });
        }

        const cart = await Cart.findOne({ user: user.userId });

        if (!cart) {
            const { response, status } = errorResponse('Cart not found', 404);
            return NextResponse.json(response, { status });
        }

        cart.items = cart.items.filter((item: any) => item.product.toString() !== productId);
        await cart.save();

        return NextResponse.json(successResponse(null, 'Item removed from cart'));
    } catch (error: any) {
        console.error('Remove from cart error:', error);
        const { response, status } = errorResponse('Failed to remove from cart');
        return NextResponse.json(response, { status });
    }
}
